const $ = require('jquery');
const _ = require('underscore');
import Humburgers from './humburgers.js'
const button = new Humburgers('hamburger', 'header-inner', 'breakpoint');
class Resize{
	constructor(){
		this.width = window.outerWidth;
	}
	
	go(){
		window.onresize = ()=>{
			if(this.width < 767){
				button.go();	
			}else{
				button.go();
			}
		}
	}
}
module.exports = Resize;